
a {
  text-decoration: none;
  // x
}


p.text-intro {
  font-size: 1.25rem;

  @include bp(sm) {
    font-size: 1.375rem;
  }
}

ul {
  padding-left: 40px;
}

h1, h2, h3, .h1, .h2, .h3 {
  font-weight: normal;
  a {
  }
}

h1, .h1 {
  font-size: 2.25rem;
  line-height: 1.25;
  margin: 0 0 calc(10em / 18); // prev em(10px) on an 18px base
  @include bp(xs2) {
    line-height: 1.2;
    font-size: 3rem;
  }
}

h2, .h2 {
  font-size: 2.1875rem;
  line-height: 1.25;
  margin: 0 0 calc(10em / 18); // prev em(10px) on an 18px base
  @include bp(xs2) {
    font-size: 2.5rem;
  }
}

