h4, .h4 {
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.5;
  text-transform: uppercase;
  margin: 0 0 calc(10em / 18); // prev em(10px) on an 18px base
  @include bp(xs2) {
    font-size: 1.5rem;
  }
}

h5, .h5 {
  font-size: 0.875rem;
  font-weight: normal;
  line-height: 1;
  margin: 0 0 calc(16em / 18); // prev em(16px) on an 18px base
  @include bp(xs2) {
    font-size: 1rem;
  }
}

h6, .h6 {
  font-size: 0.875rem;
  font-weight: normal;
  line-height: calc(16/18);
  margin: 0 0 calc(16em / 18); // prev em(16px) on an 18px base
}

p {
  margin: 0 0 1em;
}

